import { Link } from 'react-router-dom';
import './Navbar.css'
import logoSuper from '../../assets/img/logoSuperSinFondo.png'
import { useContext, useEffect } from 'react';
import { UsersContext } from '../../store/usersContext';
const Navbar = () => {

    const { currentUser, setCurrentUser } = useContext(UsersContext)

    const cerrarSesion = () => {
        if (window.confirm("Estas seguro de cerrar sesión?"))
            setCurrentUser([])
    }

    useEffect(() => {
        console.log(currentUser)
        localStorage.setItem("currentUser", JSON.stringify(currentUser))
    }, [currentUser])

    return (
        <div className='containerNav '>
            <nav className="navbar navbar-expand-lg navbar-dark d-flex">
                <div className="container-fluid">
                    <Link className="navbar-brand" to='/'><img src={logoSuper} alt='' style={{ height: "60px" }} /></Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav">
                            <li className="nav-item">
                                <Link className="nav-link active" aria-current="page" to='/'>Home</Link>
                            </li>
                            <li className="nav-item">
                                <Link className="nav-link active" to='/productos'>Productos</Link>
                            </li>
                            {
                                currentUser.length > 0 && currentUser[0].admin ?
                                    <>
                                        <li className="nav-item">
                                            <Link className="nav-link" to='/productosAdmin'>Productos Admin</Link>
                                        </li>
                                        <li className="nav-item">
                                            <Link className="nav-link" to='/usuariosAdmin'>Usuarios Admin</Link>
                                        </li>
                                    </> : ""
                            }
                        </ul>
                    </div>
                    <div className='justify-content-end'>
                        <Link to={'/carrito'}>
                            <button className='buttonNavBar'><i className=" me-2 bi bi-cart4"></i>{currentUser.length > 0 ? `Carrito de ${currentUser[0].username}` : 'Ir al Carrito'}</button>
                        </Link>
                        {
                            currentUser.length === 0 ?
                                <Link className='buttonNavBar' to='/login'>Iniciar sesión</Link>
                                :
                                <Link to='/'>
                                    <button className='buttonNavBar' onClick={() => cerrarSesion()}>Cerrar sesión</button>
                                </Link>

                        }
                    </div>
                </div>
            </nav>
        </div>
    );
}

export default Navbar;