import { useContext } from 'react';
import { UsersContext } from '../../store/usersContext';
import './Carrito.css'

const Carrito = () => {

    const {carrito, setCarrito, cantProdCarrito, setCantProdCarrito} = useContext(UsersContext)
    const eliminar = (id) => {
        if(window.confirm('Estas seguro?')) {
            setCarrito(carrito.filter(producto => producto.id !== id))
            setCantProdCarrito(cantProdCarrito.filter(cant => cant.id !== id))
        }
    }
    return (
        <div className='container-carrito'>
            {carrito.length > 0 ?

                <>
                    {carrito.map(producto => (
                        <div key={producto.id} className='p-3'>
                            <div className='container-card d-flex p-2 shadow'>
                                <img style={{ height: '100px', width: '100px' }} src={producto.urlImagen} />
                                <div>
                                    <h3>{producto.nombre}</h3>
                                    <p>{producto.descripcionGrande}</p>
                                    <h4>Precio unitario = ${producto.precio} Subtotal producto = $ {producto.precio * cantProdCarrito.find(cantidad => cantidad.id === producto.id).cantidad}</h4>
                                </div>
                                <div onClick={() => eliminar(producto.id)}>
                                    <i className="bi bi-trash"></i>
                                </div>
                            </div>
                        </div>
                    ))}

                </>
                :

                <div>Carrito vacio agrega tus productos</div>
            }
        </div>
    );
}

export default Carrito;